.mobile-menu-toggle {
  font-size: 20px;
  font-weight: 700;
  display: inline-flex;
  align-items: center;
  gap: 10px;
  margin-left: 25px;
  cursor: pointer;
  font-family: $teritoryFont;

  @include media-query('mobile') {
    font-size: 18px;
  }

  button {
    border: 0;
    margin: -4px 0 0 0;
    padding: 0;
    background: none;
    position: relative;
    display: block;
    height: 25px;
    width: 20px;

    @include media-query('mobile') {
      padding-top: 0;
    }

    span {
      display: block;
      height: 2px;
      margin: 0px 0;
      width: 20px;
      background: $primaryColor;
      position: absolute;
      left: 0;
      transform: rotate();
      transition: all ease-in-out 0.3s;

      &:nth-child(1) {
        top: 5px;
      }

      &:nth-child(2) {
        top: 12px;
      }

      &:nth-child(3) {
        top: 19px;
      }
    }


  }

  &.active {
    button {
      span {
        &:nth-child(1) {
          transform: rotate(45deg);
          top: 10px;
        }

        &:nth-child(2) {
          opacity: 0;
        }

        &:nth-child(3) {
          transform: rotate(-45deg);
          top: 10px;
        }
      }
    }
  }
}

header {
