  background: #fff;
  position: relative;
  z-index: 3;
  position: sticky;
  top: 184px;
  z-index: 1020;

  @include media-query('tablet') {
    top: 208px;
  }

  @include media-query('mobile') {
    top: 192px;
  }

  .navbar {
    padding-top: 15px;
    padding-bottom: 15px;

    @include media-query('mobile') {
      padding-top: 7px;
      padding-bottom: 7px;
    }
  }

  .right-menu {
    .btn-default {
