                    border: 0;
                    display: block;
                    height: 20px;
                    width: 20px;
                    background: url('../../assets/images/burger-icon.svg')no-repeat;
                    background-position: center;
                    margin: 0px 0 0 10px;
                  }

                  span.caret {
                    // display: block;
                    // height: 2px;
                    // margin: 0 0;
                    // width: 20px;
                    // background: #011f5b;
                    // transition: all ease-in-out .3s;
                    // top: 0;
                    // position: relative;
                    // margin-left: 10px;
                    // &:before {
                    //   content: "";
                    //   position: absolute;
                    //   display: block;
                    //   height: 2px;
                    //   margin: 0 0;
                    //   width: 20px;
                    //   background: #011f5b;
                    //   transition: all ease-in-out .3s;
                    //   top: -7px;
                    // }
                    // &:after {
                    //   content: "";
                    //   position: absolute;
                    //   display: block;
                    //   height: 2px;
                    //   margin: 0 0;
                    //   width: 20px;
                    //   background: #011f5b;
                    //   transition: all ease-in-out .3s;
                    //   top: 7px;
                    // }
                  }
                }

                &:after {
                  border: 0;
                }
              }

              .tb-megamenu-submenu {
                left: 0;
                width: 100% !important;
                left: 0;
                background: #EBF2FF;

                * {
                  position: relative;
                  display: block;
                  box-shadow: none;
                  border: 0;
                  background: transparent;
                  color: #011F5B;
                  font-size: 18px;
                  line-height: 22px;
                  text-decoration: none;
                  padding-left: 0;
                  padding-right: 0;
                  text-transform: capitalize;
                }

                ul {
                  &.tb-megamenu-subnav {
                    &.level-1 {
                      .level-2 {
                        .dropdown-toggle.nav-link {
                          font-weight: 700;
                          font-size: 18px;
                        }
                      }

                      &.items-2 {

                        &>li {
                          &.tb-megamenu-item {
                            &.level-2 {
                              &>a {
                                font-weight: bold;
                                font-size: 18px;
                                color: #011F5B;
                                text-decoration: none;
                                font-weight: bold;
                                text-transform: capitalize;
                              }
                            }
                          }
                        }
                      }

                      &.items-1 {
                        font-weight: bold;

                        li {
                          &.tb-megamenu-item {
                            &.level-2 {
                              a {
                                &.dropdown-toggle {
