                      position: relative !important;
                      overflow: visible !important;
                      margin: 0 !important;
                      margin: 0 !important;
                      width: 100% !important;
                      height: auto !important;
                      margin-bottom: 10px !important;
                      font-weight: 700;
                    }

                    input {
                      margin: 0 !important;
                      width: 100% !important;
                      height: 60px !important;
                      background: #fff;
                      border-radius: 10px;
                      padding: 15px 40px 15px 15px;
                      z-index: 999;
                      font-family: $teritoryFont;
                      text-transform: none;

                      &:-webkit-autofill {
                        background-color: transparent !important;
                        -webkit-box-shadow: 0 0 0px 1000px white inset !important;
                        transition: background-color 5000s ease-in-out 0s;
                        color: $primaryColor !important; // Ensures text remains readable
                      }

                      &::placeholder {
                        color: #02014C;
                      }
                    }
                  }

                  .form-actions,
                  [data-drupal-selector="edit-actions"] {
                    position: absolute;
                    right: 13px;
                    top: 48px;
                    z-index: 9999;

                    button {
                      font-size: 0;
                      background-image: url(/themes/custom/penchas/images/icons/search-icon.png);
                      width: 24px;
                      height: 24px;
                      background-color: transparent;
                      background-repeat: no-repeat;
                      background-position: center;
                      background-size: 19px;

                      &::before {
                        display: none;
                      }

                      &:focus {
                        outline: none;
                      }
                    }

                  }
                }
              }

              &.open {
                .dropdown-toggle.tb-megamenu-no-link {
                  span.caret {
                    background: url(../../assets/images/cross-icon.svg) no-repeat;
                  }
                }
              }
            }

            >li {
              &.tb-megamenu-item {
                >.tb-megamenu-submenu {
                  >.mega-dropdown-inner {
                    max-width: 1350px;
                    margin: auto;
                    padding-left: 12px;
                    padding-right: 12px;
                  }
                }
              }
            }

            .menu-hamburguer {
              span {

                &:first-child {
                  display: block;
                  height: 2px;
                  margin: 0 0;
                  right: 0;
                  width: 20px;
                  background: #011f5b;
                  position: absolute;
                  left: auto;
                  transform: rotate();
                  transition: all ease-in-out .3s;
                  top: 20px;
                }

                &:nth-child(2) {
                  display: block;
                  height: 2px;
                  margin: 0 0;
                  right: 0;
                  width: 20px;
                  background: #011f5b;
                  position: absolute;
                  left: auto;
                  transform: rotate();
                  transition: all ease-in-out .3s;
                  top: 27px;
                }

                &:nth-child(3) {
                  display: block;
                  height: 2px;
                  margin: 0 0;
                  right: 0;
                  width: 20px;
                  background: #011f5b;
                  position: absolute;
                  left: auto;
                  transform: rotate();
                  transition: all ease-in-out .3s;
                  top: 34px;
                }

                &.caret {
                  display: none;
                }
              }
            }
          }

          &>.nav-collapse {
            &>ul {
              &.tb-megamenu-nav {
                &>li {
                  &.tb-megamenu-item {
                    &>.tb-megamenu-submenu {
                      &.dropdown-menu {
                        padding: 60px;
                        border: 0;
                        top: 100px;
                        overflow: hidden;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

}

